<template>
  <div>
    <top-bar ref="topBar"></top-bar>
    <div class="container">
      <header>
        <span>{{$t('lang.dataSourcePage')}}</span>
      </header>
      <p style="margin:40px 0 30px 0;line-height: 32px;">{{$t('lang.dataContent')}}</p>
      <div class="source-pane">
        <div class="pane-item" v-for="(item,index) in sourceData" :key="index">
          <div class="item-content">
            <div class="item-title">
              <p>{{item.year | lang($t('lang.btnName'))}}</p>
              <span @click="open(item,index)">
                <span v-if="!item.openFlag">{{$t('lang.show')}}</span>
                <span v-else>{{$t('lang.hidden')}}</span>
              </span>
            </div>
            <div ref="liCon" class="item-body">
              <div style="margin:15px 0 0px 0">
                <p v-for="(source, i) in item.list" :key="i">{{source}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <bottom-bar></bottom-bar>
  </div>
</template>

<script>
import topBar from "@/components/top-bar";
import bottomBar from "@/components/bottom-bar";
import Vue from "vue";
import { mapActions } from "vuex";
import axios from "axios";
export default {
  components: {
    topBar,
    bottomBar
  },
  data: () => {
    return {
      liConHeight: 0,
      sourceData: []
    };
  },
  created() {
    this.network().runApiToGetSourceData();
    this.changeFalse();
    console.log(this.$store.state);
  },
  mounted() {
    this.$refs.topBar.activeIndex = 2;
  },
  computed: {},
  watch: {},
  methods: {
    ...mapActions(["changeFalse"]),
    open(item, i) {
      const liCon = this.$refs.liCon[i];
      var height = liCon.offsetHeight;
      if (height === this.liConHeight) {
        // 展开
        liCon.style.height = "auto";
        height = liCon.offsetHeight;
        liCon.style.height = this.liConHeight + "px";
        var f = document.body.offsetHeight; // 必加
        liCon.style.height = height + "px";
      } else {
        // 收缩
        liCon.style.height = this.liConHeight + "px";
      }
      if (!item.openFlag) {
        Vue.set(item, "openFlag", true);
      } else {
        Vue.set(item, "openFlag", false);
      }
    },
    event() {
      return {
        isShow: () => {}
      };
    },
    network() {
      return {
        runApiToGetSourceData: () => {
          axios
            .get("source/sourceData.json")
            .then(res => {
              console.log(res);
              this.sourceData = res.data;
            })
            .catch(err => {
              console.log(err);
            });
        }
      };
    }
  },
  filters:{
    lang(value, lang){
      if (lang == 'en') {
        if (Number(value)) {
          return value;
        } else {
          return 'other'
        }
      } else if (lang == 'zh') {
        if (Number(value)) {
          return value + '年';
        } else {
          return '其他'
        }
      }
    }
  }
};
</script>

